import React from 'react';
import { useState } from 'react';

const Buttons = props => {
	const [click, setClick] = useState(1);
	const [btnWaiting, setBtnWaiting] = useState(false);

	const handleDbClick = () => {
		setClick(click + 1);

		setTimeout(() => {
			if (click === 2 && props.stopwatchOn !== 'stop') {
				setBtnWaiting(true);
				props.setStopwatchOn('wait');
			} else {
				setClick(1);
			}
		}, 300);
	};
	return (
		<div className='buttons'>
			{(props.stopwatchOn === 'stop') || btnWaiting ? (
				<button
					className='start'
					onClick={() => {
						if(btnWaiting) setBtnWaiting(!btnWaiting);
						props.setStopwatchOn('start');
					}}>
					Start
				</button>
			) : (
				<button
					className='stop'
					onClick={() => {

						props.setStopwatchOn('stop');
					}}>
					Stop
				</button>
			)}

			<button className='wait' onClick={handleDbClick}>
				Wait
			</button>
			<button
				className='reset'
				onClick={() => {
					if(btnWaiting) setBtnWaiting(!btnWaiting);
					props.setStopwatchOn('reset');
				}}>
				Reset
			</button>
		</div>
	);
};

export default Buttons;
