import React from 'react';

const Time = ({ time: {h,s,m} }) => {
	return (
		<div className='stopwatch'>
			<span className="time">{h < 10 ? `0${h}` : h} </span>:
			<span className="time"> {m < 10 ? `0${m}` : m} </span>:
			<span className="time"> {s < 10 ? `0${s}` : s}</span>	
		</div>
	);
};

export default Time;
