import './App.css';
import { useState, useEffect } from 'react';
import { interval, Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import Time from './components/Time';
import Buttons from './components/Buttons';

const App = () => {
	const [time, setTime] = useState({ s: 0, m: 0, h: 0 });

	const [stopwatchOn, setStopwatchOn] = useState('stop');

	useEffect(() => {
		const unsubscribe$ = new Subject();
		let updatedS = time.s,
			updatedM = time.m,
			updatedH = time.h;

		interval(1000)
			.pipe(takeUntil(unsubscribe$))
			.subscribe(() => {
				switch (stopwatchOn) {
					case 'start': {
						updatedS++;
						if (updatedS > 59) {
							updatedM++;
							updatedS = 0;
						}
						if (updatedM > 59) {
							updatedH++;
							updatedM = 0;
						}
						return setTime({ s: updatedS, m: updatedM, h: updatedH });
					}
					case 'stop': {
						setTime({ s: 0, m: 0, h: 0 });
						setStopwatchOn('stop');
						break;
					}
					case 'wait': {
						unsubscribe$.complete();
						break;
					}
					case 'reset': {
						setTime({ s: 0, m: 0, h: 0 });
						setStopwatchOn('start');
						break;
					}
					default:
						break;
				}
			});
		return () => {
			unsubscribe$.next();
			unsubscribe$.complete();
		};
	}, [time.h, time.s, time.m, stopwatchOn]);

	return (
		<div className='container'>
			<Time time={time} />
			<Buttons setStopwatchOn={setStopwatchOn} stopwatchOn={stopwatchOn} />
		</div>
	);
};

export default App;
